* {
  box-sizing: border-box; }

html {
  font-size: 62.5%; }

body {
  font-family: Helvetica, Arial, sans-serif;
  font-size: 1.6rem;
  background: #ffffff;
  color: #222222;
  line-height: 1.6; }

button {
  cursor: pointer; }

button:disabled {
  cursor: default; }

.is-active {
  font-weight: bold; }

.box {
  background: url("/images/bg2.jpg");
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw; }

.box__card {
  background: rgba(204, 204, 204, 0.85);
  padding: 3.2rem 1.6rem;
  width: 30rem;
  text-align: center;
  border-radius: 4px; }

.box__title {
  margin: 0 0 1.6rem 0;
  line-height: 1; }

.button {
  background: #289fe6;
  color: #ffffff;
  border: none;
  padding: 1.2rem;
  font-weight: 400;
  font-size: 1.8rem;
  display: inline-block;
  text-decoration: none;
  line-height: 1; }

.button--no-bg {
  background: transparent; }

.button--warning {
  background: #e66f28; }

.header {
  background: #0c2f45; }

.header__title {
  color: #ffffff;
  text-decoration: none; }
  .header__title h1 {
    margin: 0; }

.header__content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.2rem 0; }

.app-container {
  margin: 0 auto;
  padding: 0 1.6rem;
  max-width: 80rem; }

.page-header {
  background: #f2f2f2;
  padding: 3.2rem 0;
  margin-bottom: 3.2rem; }

.page-header__title {
  margin: 0;
  font-weight: 300; }
  .page-header__title span {
    font-weight: bold; }

.page-header__nav {
  margin-top: 1.6rem; }

.input-group {
  display: flex;
  flex-direction: column;
  margin-bottom: 1.6rem; }
  @media (min-width: 45rem) {
    .input-group {
      flex-direction: row;
      margin-bottom: 3.2rem; } }

.input-group__item {
  margin-bottom: 1.2rem; }
  @media (min-width: 45rem) {
    .input-group__item {
      margin-right: 1.2rem;
      margin: 0 1.2rem 0 0; } }

.input-text, .input-select, .input-textarea {
  border: 1px solid #e6e6e6;
  height: 50px;
  padding: 1.2rem;
  font-weight: 300;
  font-size: 1.8rem; }

.input-select {
  color: #4f4f4f; }

.input-textarea {
  height: 10rem; }

.input-label {
  font-size: 1.2rem;
  color: #a6a6a6; }

.form {
  display: flex;
  flex-direction: column; }
  .form > * {
    margin-bottom: 1.6rem; }

.form__error {
  margin: 0 0 1.6rem 0;
  font-style: italic;
  color: red; }

@media (min-width: 45rem) {
  .mobile-visible {
    display: none; } }

@media (max-width: 44.99rem) {
  .desktop-visible {
    display: none; } }

.list-header {
  background: #f2f2f2;
  border: 1px solid #cccccc;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #3c3c3c;
  padding: 1.2rem 1.6rem; }

.list-body {
  margin-bottom: 1.6rem; }
  @media (min-width: 45rem) {
    .list-body {
      margin-bottom: 3.2rem; } }

.list-item {
  display: flex;
  flex-direction: column;
  border: 1px solid #cccccc;
  border-top: none;
  color: #343434;
  padding: 1.2rem;
  text-decoration: none;
  transition: background .3s ease; }
  .list-item:hover {
    background: #f2f2f2; }
  @media (min-width: 45rem) {
    .list-item {
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      padding: 1.6rem; } }

.list-item--message {
  align-items: center;
  justify-content: center;
  color: #cccccc;
  padding: 1.6rem; }
  .list-item--message:hover {
    background: none; }

.list-item__title {
  margin: 0;
  word-break: break-all; }

.list-item__date {
  color: #cccccc;
  font-size: 1.4rem; }

.list-item__status {
  margin: 1.2rem 0 0 0; }
  @media (min-width: 45rem) {
    .list-item__status {
      margin: 0;
      padding-left: 1.2rem; } }

.loader {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center; }

.loader__img {
  height: 6rem;
  width: 6rem; }
